import React, { useContext, useState, useEffect } from "react";
import { GlobalContext } from '../context/GlobalState'
import "../styles/overview.scss"
import $ from 'jquery';
import Typed from 'typed.js';

import Frameworks from "../components/frameworks.js"

const Overview = props => {
  const { onDarkMode } = useContext(GlobalContext);
  const [copied, setCopied] = useState(false);

  const modeClasses = onDarkMode ? "dark-mode-outline" : "light-mode-outline";

  useEffect(() => {
    const options = {
      strings: ["Product Developer^1000"],
      typeSpeed: 50,
      backSpeed: 25,
      showCursor: true,
      startDelay: 500,
      onComplete: () => { $(".typed-cursor").hide(); }
    };

    const typed = new Typed('#title-text', options);

    return () => {
      typed.destroy();
    }
  }, []);

  const handleClick = (e) => {
    var temp = document.createElement("input");
    document.body.appendChild(temp);
    temp.value = "developer@calvinhklui.com";
    temp.select();
    document.execCommand("copy");
    document.body.removeChild(temp);

    setCopied(true);
  }

  return (
    <div className="overview-container">
      <div className="introduction-container">
        <div className="words-container">
          <div className="title-container">
            <h1 className="title"><span id="title-text" /></h1>
          </div>

          <div className="subtitle-container">
            <h5 className="subtitle">
              Hello there! I'm Calvin. I enjoy working with others to <b>design</b>, <b>build</b>, and <b>ship</b> software products.
            </h5>
          </div>
        </div>

        <div className="contact-container">
          <button
            className={`contact-button ${modeClasses}`}
            onClick={handleClick}
            id="contactButton"
            value="hello"
          >
            <b>Contact</b>
          </button>
          <span className={copied ? "contact-confirmation copied" : "contact-confirmation"}>
            <b>developer@calvinhklui.com</b> copied to clipboard!
          </span>
        </div>
      </div>

      <Frameworks />
    </div>
  );
}

export default Overview;
