import React, { useContext } from "react"
import { GlobalContext } from '../context/GlobalState'
import "../styles/experiences.scss"

const Project = props => {
  const { onDarkMode } = useContext(GlobalContext);

  const modeClasses = onDarkMode ? "dark-mode-outline" : "light-mode-outline";

  return (
    <div className={`project-card ${modeClasses}`}>
      <a href={props.projectURL} target="_blank" rel="noopener noreferrer">
        <img className="project-image" src={props.projectImage} alt={props.projectName} />
      </a>
    </div>
  );
}

export default Project;
