import React, { useContext } from "react"
import { GlobalContext } from '../context/GlobalState'
import "../styles/footer.scss"

const Footer = props => {
  const { onDarkMode } = useContext(GlobalContext);
  const modeClasses = onDarkMode ? "dark-mode-footer" : "light-mode-footer";

  return (
    <div className="center-align" id="footer">
      <footer className={`footer-top ${modeClasses}`}>
        <div className="row footer-row">
          <div className="col offset-s1 s10 l12 center-align">
            <br />
            <div className="row hfooter" style={{ fontSize: "20px" }}>
              Calvin Lui &copy; 2022
            </div>
          </div>

          <div className="col offset-s1 s10 l12 center-align footer-last-line">
            <div className="row footer-row">
              Let's build something cool together.
            </div>
            <div className="row">
              Based in ðŸŒ§ Seattle.
            </div>
          </div>
        </div>
      </footer>
    </div>
  );
}

export default Footer;
