import React, { useContext, useEffect, useRef } from "react"
import { GlobalContext } from '../context/GlobalState';
import "../styles/bitmoji.scss"
import _ from "lodash"
import $ from 'jquery';

import bitmoji from "../images/bitmoji.png"
import bitmojiHover from "../images/bitmojiClosed.png"
import bitmojiScroll from "../images/bitmojiScroll.png"

const Bitmoji = props => {
  const bitmojiRef = useRef(null);
  const { toggleDarkMode } = useContext(GlobalContext);

  useEffect(() => {
    const handleScroll = (e) => {
      bitmojiRef.current.src = bitmojiScroll;
    }
  
    const handleScrollStop = _.debounce(() => {
      bitmojiRef.current.src = bitmoji;
    }, 200);

    window.addEventListener('scroll', handleScroll, { passive: true });
    window.addEventListener('scroll', handleScrollStop);

    return () => {
      window.removeEventListener('scroll', handleScroll);
      window.removeEventListener('scroll', handleScrollStop);
    }
  }, []);
  
  const handleClick = (e) => {
    $('html,body').animate({ scrollTop: 0 }, 2000);
    toggleDarkMode();
  }

  return (
    <>
      <div className="bitmoji-container">
        <div className="container center-align">
          <img
            className="responsive-img stick-to-footer"
            ref={bitmojiRef}
            id="bitmoji-welcome"
            src={bitmoji}
            alt="bitmoji"
            role="button"
            onClick={handleClick}
            onMouseOver={e => (e.currentTarget.src = bitmojiHover)}
            onFocus={e => (e.currentTarget.src = bitmojiHover)}
            onMouseOut={e => (e.currentTarget.src = bitmoji)}
            onBlur={e => (e.currentTarget.src = bitmoji)}
          />
        </div>
      </div>
    </>
  );
}

export default Bitmoji;
